import React, {Component} from 'react';

import './bootstrap.css';

class Comp1 extends Component {

    handleButtonClick() {
        const fomrVisible = this.props.fomrVisible;
        this.props.changeVisibleForm(!fomrVisible);
        console.log('fomrVisible: ', fomrVisible);
    }

    render() {
        return(
            <div className="jumbotron container margin-top">
                <h1 className="display">Маникюр, педикюр и дизайны</h1>
                <p className="lead">
                Маникюр, педикюр и модные дизайны, наращивание и восстановление ногтей
                </p>
                <hr className="my-4" />
                <p>
                </p>
                <p className="lead">
                    <a className="btn btn-danger btn-lg" href="#" role="button" onClick={()=>this.handleButtonClick()}>
                    Записаться
                    </a>
                </p>
            </div>
        )
    };
};

export default Comp1;
