import constants from '../constants';
import { initialState } from './initialState.js'
  
export const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case constants.ACTION_CHANGE_NAME:
      return { ...state, name: action.payload }
    case constants.ACTION_CHANGE_EMAIL:
      return { ...state, email: action.payload}
    case constants.ACTION_CHANGE_TEL:
      return { ...state, tel: action.payload }
    case constants.ACTION_CHANGE_DATE:
      return { ...state, date: action.payload }
    case constants.ACTION_CHANGE_INST:
      return { ...state, inst: action.payload }  
    case constants.ACTION_CHANGE_MESSAGE:
      return { ...state, message: action.payload }  
    case constants.ACTION_CHANGE_VISIBLE_FORM:
      return { ...state, fomrVisible: action.payload }
    case constants.ACTION_CHANGE_DATA:
      return { ...state, data: action.payload }
    case constants.ACTION_GET_INITIAL_STATE:
      return { ...state, name: "", email: "", tel: "", date: "", inst: "", message: "" }
  } 
  return state;
};
  
