import React, { Component } from 'react';
import InputMask from 'react-input-mask';

import api from '../api';

class Form extends Component {

    handleClickButtonSubmit() {
        console.log(this.props.props.name);
        console.log(this.props.props.email);
        console.log(this.props.props.tel);
        console.log(this.props.props.date);
        console.log(this.props.props.inst);
        console.log(this.props.props.message);
        if (this.props.props.name !=='' && this.props.props.email !=='' && this.props.props.tel !=='' && this.props.props.date !=='' && this.props.props.inst !=='') {
            console.log('OK');
            const dataClient = {
                username: this.props.props.name,
                theDate: this.props.props.date,
                tel: this.props.props.tel,
                inst: this.props.props.inst,
                message: this.props.props.message
            }
            api.setClients(dataClient).then(function (response) {
                console.log(response);
              });
            
            
        } else {
            alert ('Заполните все поля!')
        }
    }

    componentDidMount() {
        this.nameInput.focus();
    }

    render() {
        return (
            <div className="row">
                <div className="col-lg-12">
                    <div className="bs-component">
                        <form>
                            <fieldset>
                                <legend>Введите данные для записи</legend>
                                <div className="form-group">
                                    <label htmlFor="exampleInputName">Имя</label>
                                    <input
                                        ref={(input) => { this.nameInput = input; }}
                                        type="text"
                                        name="nameInput"
                                        className="form-control"
                                        id="exampleInputName"
                                        aria-describedby="nameHelp"
                                        placeholder="Введите ваше Имя"
                                        value={this.props.props.name}
                                        onChange={(event)=>{
                                            this.props.props.changeName(event.target.value);
                                        }}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="exampleInputEmail1">Email-адрес</label>
                                    <input
                                        value={this.props.props.email}
                                        type="email"
                                        name="emailInput"
                                        className="form-control"
                                        id="exampleInputEmail1"
                                        aria-describedby="emailHelp"
                                        placeholder="Введите ваш email"
                                        onChange={(event)=>{
                                            this.props.props.changeEmail(event.target.value);
                                        }}
                                    />
                                    <small id="emailHelp" className="form-text text-muted">
                                        Мы никогда не будем делиться вашей электронной почтой с кем-либо еще.
                                    </small>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="exampleInputTel">Телефон</label>
                                    <InputMask
                                        value={this.props.props.tel}
                                        type="tel"
                                        className="form-control"
                                        id="exampleInputTel"
                                        mask="+7 (999) 999-99-99"
                                        placeholder="+7 (___) ___-__-__"
                                        onChange={(event)=>{
                                            this.props.props.changeTel(event.target.value);
                                        }}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="exampleInputDate">Желаемая дата посещения</label>
                                    <input
                                        value={this.props.props.date}
                                        type="date"
                                        className="form-control"
                                        id="exampleInputDate"
                                        mask= '99/99/9999'
                                        placeholder="__/__/____"
                                        onChange={(event)=>{
                                            this.props.props.changeDate(event.target.value);
                                        }}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="exampleInputInst">Instagram / vk.com</label>
                                    <input
                                        value={this.props.props.inst}
                                        type="text"
                                        name="instInput"
                                        className="form-control"
                                        id="exampleInputInst"
                                        aria-describedby="nameHelp"
                                        placeholder="Введите ссылку на ваш профиль в Instagram / vk.com"
                                        onChange={(event)=>{
                                            this.props.props.changeInst(event.target.value);
                                        }}
                                    />
                                </div>                             
                                <div className="form-group">
                                    <label htmlFor="exampleTextarea">Примечания</label>
                                    <textarea
                                        value={this.props.props.message}
                                        className="form-control"
                                        id="exampleTextarea"
                                        rows={3}
                                        onChange={(event)=>{
                                            this.props.props.changeMessage(event.target.value);
                                        }}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="submitButton">Нажимая на кнопку, Вы даете свое согласие на обработку персональных данных</label>
                                    <div className="form-group">
                                        <button id="submitButton" type="button" className="btn btn-primary" onClick={() => {this.handleClickButtonSubmit();this.props.props.getInitialState()}}>
                                        Записаться
                                        </button>
                                    </div>
                                </div>
                            </fieldset>
                        </form>

                    </div>
                </div>
            </div>
        )
    }
};
export default Form;
