import React, { Component } from 'react';

import api from '../api';

import Header from './Header.js';
import Comp1 from './Comp1';
import Cards from './Cards.js';
import Form from './Form.js';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { changeName, changeEmail, changeTel, changeDate, changeInst, changeMessage, changeData, getInitialState, changeVisibleForm } from '../store/actions';


import './bootstrap.css';
import './App.css';




class App extends Component {

    constructor(props) {
        super(props);
        console.log('constructor(props)');
        api.getMains().then(event => { this.props.changeData(event.data)});
    }
    componentWillMount() {
      console.log('componentWillMount()');
    }
    

    componentDidMount() {
        console.log("componentDidMount()");
    }

    shouldComponentUpdate(nextProps, nextState) {     
        console.log('shouldComponentUpdate');
        return true;
    }

    render() {
        console.log('render()');
        
        return (
            <div className="App">
                <Header fomrVisible={this.props.fomrVisible} changeVisibleForm={this.props.changeVisibleForm} />
                <div className="container">
                    <div className="row">
                        <Comp1 fomrVisible={this.props.fomrVisible} changeVisibleForm={this.props.changeVisibleForm} />
                    </div>
                </div>
                <div id="formSubmitClient" className="container">
                    {this.props.fomrVisible === true ? <Form props={this.props}/> : null}
                </div>
                <div id="news" className="container">
                        <Cards infoData={this.props.data}/>
                </div>
            </div>
        );
    };
};

const mapStateToProps = (state) => {
    console.log('mapStateToProps - state, который пришел в mapStateToProps: ', state);
    return {
        name: state.name,
        email: state.email,
        tel: state.tel,
        date: state.date,
        inst: state.inst,
        message: state.message,
        data: state.data,
        fomrVisible: state.fomrVisible
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        changeName: bindActionCreators(changeName, dispatch),
        changeEmail: bindActionCreators(changeEmail, dispatch),
        changeTel: bindActionCreators(changeTel, dispatch),
        changeDate: bindActionCreators(changeDate, dispatch),
        changeInst: bindActionCreators(changeInst, dispatch),
        changeMessage: bindActionCreators(changeMessage, dispatch),
        changeData: bindActionCreators(changeData, dispatch),
        getInitialState: bindActionCreators(getInitialState, dispatch),
        changeVisibleForm: bindActionCreators(changeVisibleForm, dispatch)
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(App);