import constants from '../constants';

export const changeName = (NewName) => {
  return {
    type: constants.ACTION_CHANGE_NAME,
    payload: NewName
  }
};
  
export const changeEmail = (NewEmail) => {
  return {
    type: constants.ACTION_CHANGE_EMAIL,
    payload: NewEmail
  }
};
  
export const changeTel = (NewTel) => {
  return {
    type: constants.ACTION_CHANGE_TEL,
    payload: NewTel
  }
};
  
export const changeDate = (NewDate) => {
  return {
    type: constants.ACTION_CHANGE_DATE,
    payload: NewDate
  }
};

export const changeInst = (NewInst) => {
  return {
    type: constants.ACTION_CHANGE_INST,
    payload: NewInst
  }
};

export const changeMessage = (NewMessage) => {
  return {
    type: constants.ACTION_CHANGE_MESSAGE,
    payload: NewMessage
  }
};

export const changeData = (NewData) => {
  return {
    type: constants.ACTION_CHANGE_DATA,
    payload: NewData
  }
};

export const changeVisibleForm = (NewVisibleForm) => {
  return {
    type: constants.ACTION_CHANGE_VISIBLE_FORM,
    payload: NewVisibleForm
  }
};

export const getInitialState = () => {
  return {
    type: constants.ACTION_GET_INITIAL_STATE
  }
};