import React, { Component } from 'react';

class Card extends Component {
    render() {
        return (
            <div>
                <div className="card mb-3">
                    <h3 className="card-header">{this.props.dataCard.name}</h3>
                    <div className="card-body">
                        <h5 className="card-title">{this.props.dataCard.title}</h5>
                        <h6 className="card-subtitle text-muted">
                            {this.props.dataCard.subtitle}
                </h6>
                    </div>
                    <img
                        style={{ width: "100%", display: "block" }}
                        src={this.props.dataCard.img}
                        alt="Card image"
                    />
                    <div className="card-body">
                        <p className="card-text">
                            {this.props.dataCard.text}
                        </p>
                    </div>
                    <div className="card-body">
                        <a href={this.props.dataCard.hrefBut} className="card-link">
                            {this.props.dataCard.nameBut}
                        </a>
                    </div>
                    <div className="card-footer text-muted">{this.props.dataCard.createdAt}</div>
                </div>
            </div>
        )
    }
};
export default Card;
