import React, {Component} from 'react';

import Card from './Card.js';

class Cards extends Component {

    render() {
        
        if (this.props.infoData !== '' || undefined || null) {

            return(
                <div className="row">
                    {this.props.infoData.map(data => 
                        <div className="col-lg-4" key={data._id}>
                            <Card dataCard={data} />
                        </div>
                    )}
                </div>
                
            )
             
        } else {
            return (<div className="col-lg-4"></div>)
        }
        
        
    };
};

export default Cards;